/**
 * @author cqb 2016-03-30.
 * @module BaseComponent
 */
import React, {PureComponent} from 'react';
import Emitter from './Emitter';
import '../../lang/zh_cn';
import '../../lang/us_en';
const i18n = window.RCMUI_LANG || 'zh_cn';

if (!window.RCMUI_I18N) {
    window.RCMUI_I18N = window.RCMUI_LANGS[i18n] || {};
    console.log(window.RCMUI_I18N);
}

/**
 * BaseComponent 
 * @class BaseComponent
 * @constructor
 * @extend Component
 * @extend Emitter
 */
class BaseComponent extends PureComponent {
    displayName = 'BaseComponent';
    
    static defaultProps = {
        theme: 'default',

        disabled: false,

        visibility: true
    }

    constructor (props, context) {
        super(props, context);

        this.state = {
            theme: props.theme,
            disabled: props.disabled,
            visibility: props.visibility
        };
    }

    /**
     * state
     * @method addState
     * @param params
     */
    addState (params) {
        if (!this.state) {
            this.state = {};
        }
        for (const i in params) {
            if ({}.hasOwnProperty.call(params, i)) {
                this.state[i] = params[i];
            }
        }
    }

    /**
     * ,beforeShow
     * beforeShow,
     * falsetrueshow
     * shown
     * @method show
     * @chain
     * @return {Object}
     */
    show () {
        let ret = this.emit('beforeShow');
        ret = ret === undefined ? true : ret;
        if (ret && !this.state.visibility) {
            this.emit('show');
            this.setState({visibility: true});
            this.emit('shown');
        }
        return this;
    }

    /**
     * , beforeHide 
     * beforeHide ,
     * falsetrue hide
     *  hidden 
     * @method hide
     * @chain
     * @return {Object}
     */
    hide () {
        let ret = this.emit('beforeHide');
        ret = ret === undefined ? true : ret;
        if (ret && this.state.visibility) {
            this.emit('hide');
            this.setState({visibility: false});
            this.emit('hidden');
        }
        return this;
    }

    /**
     * 
     * @method getValue
     * @returns {null}
     */
    getValue () {
        return null;
    }

    /**
     * 
     * @method setValue
     * @param args {*}
     */
    setValue () {

    }

    /**
     * 
     * @method setTheme
     * @param theme {String} default primary dark
     */
    setTheme (theme) {
        this.setState({theme});
    }

    /**
     * 
     * @method getTheme
     * @returns {String}
     */
    getTheme () {
        return this.state.theme;
    }

    /**
     * 
     * @method disable
     * @return {Boolean} 
     */
    disable () {
        this.setState({disabled: true});
    }

    /**
     * 
     * @method disable
     * @return {Boolean} 
     */
    enable () {
        this.setState({disabled: false});
    }

    /**
     * 
     * @method destroy
     */
    destroy () {

    }

    /**
     * IE9
     * @method isLtIE9
     * @returns {boolean}
     */
    isLtIE9 () {
        if (navigator.userAgent.indexOf('MSIE') > 0) {
            if (navigator.userAgent.indexOf('MSIE 6.0') > 0) {
                return true;
            } else if (navigator.userAgent.indexOf('MSIE 7.0') > 0) {
                return true;
            } else if (navigator.userAgent.indexOf('MSIE 8.0') > 0) {
                return true;
            } else if (navigator.userAgent.indexOf('MSIE 9.0') > 0) {
                return true;
            } else {
                return false;
            }
        } else {
            return false;
        }
    }

    /**
     * 
     * @method render
     * @returns {XML}
     */
    render () {
        return (<div />);
    }
}

BaseComponent.propTypes = {};

Emitter.inherits(BaseComponent);

export default BaseComponent;



