/**
 * @author cqb 2016-03-24
 * @module 
 */

/**
 * 
 * @class Core
 * @static
 * @constructor
 */
class Core {
    /**
     * 
     * @param obj
     * @param type
     * @returns {boolean}
     */
    static isType(obj, type) {
        return Object.prototype.toString.apply(obj) === '[object ' + type + ']';
    }

    /**
     * 
     * @param obj
     * @returns {boolean} 
     */
    static isArray(obj){
        return Core.isType(obj, 'Array');
    }

    /**
     * 
     * @param obj
     * @returns {boolean} 
     */
    static isString(obj){
        return Core.isType(obj, 'String');
    }

    /**
     * 
     * @param obj
     * @returns {boolean} 
     */
    static isNumber(obj){
        return Core.isType(obj, 'Number');
    }

    /**
     * 
     * @param obj
     * @returns {boolean} 
     */
    static isObject(obj){
        return Core.isType(obj, 'Object');
    }

    /**
     * 
     * @param obj
     * @returns {boolean} 
     */
    static isNull(obj){
        return Core.isType(obj, 'Null');
    }

    /**
     * 
     * @param obj
     * @returns {boolean} 
     */
    static isUndefined(obj){
        return obj === undefined || Core.isType(obj, 'Undefined');
    }

    /**
     * 
     * @param fun
     * @returns {boolean} 
     */
    static isFunction(fun){
        return Core.isType(fun, 'Function');
    }

    /**
     * 
     * @param obj
     * @returns {boolean}
     */
    static isDefined(obj){
        return !Core.isUndefined(obj);
    }

    /**
     * 
     * @param obj {Object} 
     * @returns {*}
     */
    static clone(obj){
        let o;
        if (typeof obj == 'object') {
            if (obj === null) {
                o = null;
            } else {
                if (Core.isArray(obj)) {
                    o = [];
                    for (let i = 0, len = obj.length; i < len; i++) {
                        o.push(Core.clone(obj[i]));
                    }
                } else {
                    o = {};
                    for (let j in obj) {
                        if({}.propertyIsEnumerable.call(obj, j)){
                            o[j] = Core.clone(obj[j]);
                        }
                    }
                }
            }
        } else {
            o = obj;
        }
        return o;
    }
}

export default Core;



